<template>
    <div id="background">
    <form v-on:submit.prevent ="request">
         <b-field class="centerme">
            <b-input placeholder="Search..."
                class="searchbox"
                type="search"
                icon-pack="fas"
                icon="search"
                v-model="searchText"
               >
            </b-input>
        </b-field>
    </form>
    <br>
    <span v-if="resultfound" class="allignme">Search Results for <span >{{this.resultname}}</span>..</span>
    <br><br>
    <div style="background:#f6f5fa">
        <section class="columns is-multiline" >
        <figure v-for="image in images" :key="image.id" class="column is-one-third">
          <img :src='`${image.webformatURL}`'>
      </figure>
        </section>
    </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            searchText: '',
            previousText: this.$route.params.message,
            apiUrl:'https://pixabay.com/api/',
            apiKey:'23294204-5ba3b34b4c9aaa6b3bef520eb',
            images:[],
            resultfound: false,
            resultname:''
        };
    },
    async created() {
            try {
             const res = await axios.get(`${this.apiUrl}/?key=${this.apiKey}&q=${this.previousText}&image_type=photo&per_page=30&safesearch=true`)
             this.images = res.data.hits
             this.resultfound = !this.resultfound
             this.resultname = this.previousText
             console.log(this.images)
            }catch(err) {
              console.log(err)
            }
     },
     methods: {
         async request() {
            try {
             const res = await axios.get(`${this.apiUrl}/?key=${this.apiKey}&q=${this.searchText}&image_type=photo&per_page=30&safesearch=true`)
             this.images = res.data.hits
             console.log(this.images)
             this.resultname = this.searchText
            }catch(err) {
              console.log(err)
            }            
         }
     }
}
</script>

<style scoped>
.centerme {
    padding-top: 5vh ;
}
.searchbox {
  width: 96vw;
  margin-left: 1vw;
  margin-right: 1vw;
}
.allignme {
    padding-left: 10px;
}
.image{ 
    max-width: 50vw;
}
#background {
    background-color: rgb(221, 233, 236);
}
</style>